  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
} */

.inner-container {
  height: 100px;
  display: flex;
  flex-grow: 1;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 24pt;
  font-weight: bolder;
  background-color: rgb(195, 125, 65);
}

.game-board {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  flex-direction: row;
  align-content: center;
}

.picture {
  background-color: white;
  margin: 20px;
  width: 200px;
  height: 200px;
  border: 2px solid black;
}

.game-title {
  font-size: 32pt;
  font-weight: bolder;
  text-transform: uppercase;
  text-align: center;
}

.instructions {
  font-size: 22pt;
  text-align: center;
}
